<template>
  <div>
    <div class="success" v-if="user">  ({{ user.email }})</div>
    <LoginForm @loginSuccess="login" v-else></LoginForm>
  </div>
</template>

<script>
import LoginForm from "@/components/LoginForm.vue";
export default {
  methods: {
    login(user) {
      // this.user = user;
      //   assign     .
      this.$store.state.user.loginUser = user;
      //
      console.log("[] ", user, this.$route);
      const nextUrl = this.$route.query.redirect || "/";
      this.$router.replace({ path: nextUrl });

      /*
      if (this.$route.query.redirect) {
        this.$router.push({ path: this.$route.query.redirect });
      } else {
        this.$router.push({ path: "/" });
      }
      */
    },
  },
  computed: {
    user() {
      return this.$store.state.user.loginUser;
    },
  },
  watch: {
    user(cur, prev) {
      console.log("[]", cur, prev);
      const nextUrl = this.$route.query.redirect || "/";
      this.$router.push({ path: nextUrl });
    },
  },
  components: {
    LoginForm,
  },
};
</script>

