<template>
  <div>
    <form id="loginForm">
      <div class="form-group">
        <label for="email"> </label>
        <input type="text" class="form-control" v-model="hint.email" />
      </div>
      <select class="sel" size="1" v-model="hint.pwhint">
        <option value=""></option>
        <option v-for="pwhintList in pwhintLists" :key="pwhintList.value">
          {{ pwhintList.text }}
        </option>
      </select>
      <div class="form-group">
        <label for="hint"></label>
        <input type="text" class="form-control" v-model="hint.pwhintans" />
      </div>
    </form>
    <button @click="(e) => sendHint(e)"></button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pwhintLists: [
        {
          value: 1,
          text: "  ?",
        },
        {
          value: 2,
          text: " ?",
        },
        {
          value: 3,
          text: " ?",
        },
        {
          value: 4,
          text: "   ?",
        },
        {
          value: 5,
          text: " ?",
        },
      ],
      hint: {
        email: null,
        pwhint: "",
        pwhintans: null,
      },
    };
  },
  methods: {
    sendHint() {
      this.$emit("hint", {
        email: this.hint.email,
        pwhint: this.hint.pwhint,
        pwAns: this.hint.pwhintans,
      });
    },
  },
};
</script>

<style lang="scss" scoped></style>
