<template>
  <table class="table">
    <tr>
      <th></th>
      <th></th>
      <th></th>
      <th></th>
      <th></th>
    </tr>
    <tr v-for="ban in banList" :key="ban.seq" @click="banClicked(ban)">
      <td>{{ ban.seq }}</td>
      <td>{{ timeStampToDate(new Date(ban.repotertime)) }}</td>
      <td class="ban-type">
        <span> {{ banText(ban.bancode) }}</span>
      </td>
      <td>{{ getContent(ban) }}</td>
      <td>{{ ban.ID }}</td>
    </tr>
  </table>
</template>

<script>
import util from "../../service/util";
export default {
  props: ["banList"],
  methods: {
    banText(banCode) {
      console.log("[### post]", banCode);
      if (banCode == "AD") {
        return "";
      } else if (banCode == "PN") {
        return "";
      } else if (banCode == "AB") {
        return ",";
      } else if (banCode == "GM") {
        return "";
      } else if (banCode == "ET") {
        return "";
      }
    },
    timeStampToDate(date) {
      return util.formatDate(date);
    },
    getContent(ban) {
      if (this.banType === "P") {
        return ban.CONTENTS;
      } else {
        return ban.CONTENT;
      }
    },
    banClicked(ban) {
      this.$emit("banclick", ban);
    },
  },
};
</script>

<style></style>
